import { build } from './builder'

const urlPrefix = '/good_group'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search),
  uniqProp: 'goodGroupId'
}

const {
  search: searchGoodGroups,
  get: getGoodGroups,
  post: postGoodGroup,
  put: putGoodGroup,
  delete: deleteGoodGroup
} = build(urlPrefix, polyfill)

export { searchGoodGroups, getGoodGroups, postGoodGroup, putGoodGroup, deleteGoodGroup }
