<template>
  <v-container pa-0>
    <v-layout v-if="value != null" wrap>
      <v-flex xs12>
        <v-timeline v-if="items.length" :dense="true" class="mx-3">
          <v-timeline-item
            v-for="({ changeDate, oldValue, newValue }, i) in items"
            :key="'timeline_item_' + i"
            color="primary"
            small
          >
            <v-layout pt-3>
              <v-flex xs4>
                <strong>{{ $d(new Date(changeDate), 'date') }}</strong>
                <br>
                {{ $d(new Date(changeDate), 'time') }},
                {{ $d(new Date(changeDate), 'weekday') }}
              </v-flex>
              <v-flex xs8>
                {{ $t('new-value') }}:
                <strong>{{ newValue }}</strong>
                <br>
                {{ $t('old-value') }}:
                <strong>{{ oldValue }}</strong>
              </v-flex>
            </v-layout>
          </v-timeline-item>
        </v-timeline>
      </v-flex>
      <v-flex xs12>
        <v-btn flat block color="primary" :loading="loading" @click="load()">
          {{ $t(items.length ? 'load-more' : 'load-change-log') }}
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { getCarHistory } from '@/axios'

export default {
  props: {
    value: Number
  },
  data: () => ({
    items: [],
    loading: false
  }),
  watch: {
    value (v) {
      if (v != null) {
        this.items.splice(0)
      }
    }
  },
  methods: {
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const items = await getCarHistory(this.value, this.items.length, 3)
        if (items.length) {
          this.items.push(...items)
        }
      } catch (error) {
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
