import { build } from './builder'

const urlPrefix = '/lease'
const polyfill = {
  test: (v, search) =>
    v.start.toLowerCase().includes(search) ||
    v.end.toLowerCase().includes(search) ||
    v.lessee.name.toLowerCase().includes(search) ||
    v.lessee.email.toLowerCase().includes(search) ||
    v.lessee.telephone.toLowerCase().includes(search) ||
    v.lessee.ico.toLowerCase().includes(search) ||
    v.car.name.toLowerCase().includes(search),
  uniqProp: 'leaseId'
}

const {
  search: searchLeases,
  get: getLeases,
  post: postLease,
  put: putLease,
  delete: deleteLease
} = build(urlPrefix, polyfill)

export { searchLeases, getLeases, postLease, putLease, deleteLease }
