import { build } from './builder'

const urlPrefix = '/defect'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    v.description.toLowerCase().includes(search) ||
    v.date.toLowerCase().includes(search) ||
    v.car.name.toLowerCase().includes(search),
  uniqProp: 'defectId'
}

const {
  search: searchDefects,
  get: getDefects,
  post: postDefect,
  put: putDefect,
  delete: deleteDefect
} = build(urlPrefix, polyfill)

export { searchDefects, getDefects, postDefect, putDefect, deleteDefect }
