<template>
  <Scroller :loading="loading" @load="load()">
    <v-flex v-if="xhrs.searchItems" xs12 py-5>
      <v-text-field
        v-model="search" solo
        clearable
        style="max-width: 600px; margin: 0 auto;"
        @keyup.enter="load(true)"
        @click:clear="() => { search = null; load(true) }"
      >
        <v-btn slot="append" flat color="primary" @click="load(true)" v-text="$t('search')" />
      </v-text-field>
    </v-flex>

    <v-flex v-for="(group, i) in groups" :key="`group_${i18nKey}_${i}`" xs12 sm6 md4 lg3 xl2>
      <v-card v-for="({ item, index }) in group" :key="`card_${i18nKey}_${index}`" class="mx-2 mb-3">
        <v-card-title class="headline">
          <slot :item="item" name="headline" />
        </v-card-title>
        <v-card-text class="text-xs-left" style="display: flex; align-items: center">
          <slot :item="item" />
        </v-card-text>
        <v-card-actions v-if="rw">
          <v-spacer />
          <v-btn flat color="primary" @click="edit(index)" v-text="$t('edit')" />
          <v-btn flat color="primary" @click="remove(index)" v-text="$t('delete')" />
        </v-card-actions>
      </v-card>
    </v-flex>

    <slot
      :item="items[index] || createNewItem()"
      :itemCb="save" :show="show"
      :showCb="editShowCb" name="edit"
    />

    <v-btn v-if="rw" color="accent" fixed dark fab bottom right @click="edit(items.length)">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </Scroller>
</template>

<script>
import Scroller from './Scroller'

export default {
  components: { Scroller },
  props: {
    i18nKey: { type: String, default: '' },
    idProp: { type: String, default: 'id' },
    newItem: { type: Object, default: () => ({}) },
    xhrs: { type: Object, default: () => ({}) }
  },
  data: () => ({
    index: -1,
    items: [],
    loading: false,
    search: null,
    show: false
  }),
  computed: {
    columns () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 1
        case 'sm': return 2
        case 'md': return 3
        case 'lg': return 4
        case 'xl': return 6
        default: return 1
      }
    },
    itemsPerLoad () {
      return 3 * this.columns
    },
    groups () {
      const items = this.items.map((item, index) => ({ item, index }))
      return [...Array(this.columns)].map((_, group) =>
        items.filter((_, i) =>
          i % this.columns === group
        )
      )
    },
    permission () {
      return this.$store.getters.isAllowed(this.$route.name)
    },
    rw () {
      return this.permission === 'rw'
    }
  },
  methods: {
    showAlert (type, result) {
      this.$store.commit('setAlert', {
        type: result,
        text: this.$t(
          `xhr-feedback.${type}.${result}`,
          { item: this.$t(this.i18nKey) }
        )
      })
    },
    edit (i) {
      this.index = i
      this.show = true
    },
    async save (item) {
      const oldItem = this.items[this.index]
      this.$set(this.items, this.index, item)
      if (item[this.idProp] != null) {
        try {
          this.$store.commit('setWorking', true)
          await this.xhrs.putItem(JSON.parse(JSON.stringify(item)))
          this.showAlert('put', 'success')
        } catch (error) {
          this.showAlert('put', 'error')
          this.$set(
            this.items,
            this.items.indexOf(item),
            oldItem
          )
        } finally {
          this.$store.commit('setWorking', false)
        }
      } else {
        try {
          this.$store.commit('setWorking', true)
          const id = await this.xhrs.postItem(JSON.parse(JSON.stringify(item)))
          item[this.idProp] = id
          this.showAlert('post', 'success')
        } catch (error) {
          this.showAlert('post', 'error')
          this.$set(
            this.items,
            this.items.indexOf(item),
            oldItem
          )
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async remove (i) {
      if (confirm(this.$t('confirm-delete'))) {
        const item = this.items.splice(i, 1)[0]
        try {
          this.$store.commit('setWorking', true)
          await this.xhrs.deleteItem(item[this.idProp])
          this.showAlert('delete', 'success')
        } catch (error) {
          this.items.splice(i, 0, item)
          this.showAlert('delete', 'error')
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const items = this.search != null && this.search !== ''
          ? await this.xhrs.searchItems(this.search, this.items.length, this.itemsPerLoad)
          : await this.xhrs.getItems(this.items.length, this.itemsPerLoad)
        if (items.length) {
          this.items.push(...items)
        }
      } catch (error) {
      } finally {
        this.loading = false
      }
    },
    createNewItem () {
      return JSON.parse(JSON.stringify(this.newItem))
    },
    editShowCb (val) {
      this.show = val
    }
  }
}
</script>
