import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const common = {
  'logout': true,
  'home': true
}
const accessRules = {
  'root': true,
  'admin': {
    'administration': 'rw',
    ...common
  },
  'other': {
    'login': true
  },
  'externalUser': {
    'defects': 'ro',
    'repairs': 'ro',
    'revisions': 'ro',
    ...common
  },
  'manager': {
    'administration': 'ro',
    'cars': 'rw',
    'defects': 'rw',
    'leases': 'rw',
    'repairs': 'rw',
    'revisions': 'rw',
    'subjects': 'rw',
    ...common
  },
  'serviceTechnician': {
    'cars': 'ro',
    'defects': 'rw',
    'repairs': 'rw',
    'revisions': 'rw',
    ...common
  }
}

export default new Vuex.Store({
  state: {
    role: null,
    working: false,
    alert: {
      show: false,
      text: '',
      type: 'info'
    }
  },
  mutations: {
    setRole (state, role) {
      state.role = role
    },
    setWorking (state, working) {
      state.working = working
    },
    setAlert (state, { type, text }) {
      state.alert = {
        show: true,
        type,
        text
      }
    },
    clearAlert (state) {
      state.alert.show = false
    }
  },
  getters: {
    isAllowed: state => {
      const rules = accessRules[state.role]
      if (rules === true) { // Superuser
        return name => name !== 'login' && 'rw'
      } else if (rules) { // Logged in user
        return name => rules[name]
      } else { // Others
        return name => accessRules.other[name]
      }
    }
  }
})
