import { build } from './builder'

const urlPrefix = '/revision'
const polyfill = {
  test: (v, search) => v.description.toLowerCase().includes(search) ||
    v.date.toLowerCase().includes(search) ||
    v.car.name.toLowerCase().includes(search),
  uniqProp: 'revisionId'
}

const {
  search: searchRevisions,
  get: getRevisions,
  post: postRevision,
  put: putRevision,
  delete: deleteRevision
} = build(urlPrefix, polyfill)

export { searchRevisions, getRevisions, postRevision, putRevision, deleteRevision }
