<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper :headline="$t('lease')" :value="show" :save-btn="valid" @save="save" @cancel="cancel">
      <v-flex xs12>
        <RemoteSelect
          v-model="item.car"
          :label="$t('car')"
          :rules="[rules.required]"
          :search-items="searchCars"
          :format-text="formatCarNumberRegularSpace"
          item-text="name"
          item-value="carId"
        />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.start" :label="$t('since')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.end" :label="$t('to')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.lessee"
          :label="$t('lessee')"
          :rules="[rules.required]"
          :search-items="searchSubjects"
          item-text="name"
          item-value="subjectId"
        />
      </v-flex>
      <v-flex xs12>
        <v-switch v-model="lease" :label="$t(`switch.lease.${item.lease || 0}`)" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { formatCarNumberRegularSpace } from '@/common'
import { searchCars, searchSubjects } from '@/axios'

export default {
  components: { DateTime, RemoteSelect },
  mixins: [DialogMixin],
  data: () => ({
    formatCarNumberRegularSpace,
    searchCars,
    searchSubjects
  }),
  computed: {
    lease: {
      get () {
        return this.item.lease !== 0
      },
      set (v) {
        this.item.lease = v ? 1 : 0
      }
    }
  }
}
</script>
