import { build } from './builder'

const urlPrefix = '/repair'
const polyfill = {
  test: (v, search) => v.description.toLowerCase().includes(search) ||
   v.date.toLowerCase().includes(search) ||
   v.defect.name.toLowerCase().includes(search) ||
   v.defect.car.name.toLowerCase().includes(search),
  uniqProp: 'repairId'
}

const {
  search: searchRepairs,
  get: getRepairs,
  post: postRepair,
  put: putRepair,
  delete: deleteRepair
} = build(urlPrefix, polyfill)

export { searchRepairs, getRepairs, postRepair, putRepair, deleteRepair }
